	module.exports = {"data":{"markdownRemark":{"html":"<p>散步到天安门广场。这里洋溢着一片节日气氛，国庆节就要来临。天安门城楼装修一新，红色的城墙粉刷过了。少先队员们站立在金水河畔。</p>\n<p>广场上有很大的变化，大片的绿化地带在广场中间形成。东面是用花卉组成的龙，西面是凤凰，后面有一座用花组成的宝塔，还有艘船的模型，命名为\"海上世界\"。众多的人聚集在广场上，喜气洋洋，拍照的拍照，观赏的观赏。长安街上巨大的标语树立起来，国旗在后面迎风飘扬。共和国的生日要来了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0147.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0147.md","frontmatter":{"title":"9月24日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0147.md absPath of file >>> MarkdownRemark"}}

